,[_vm._v("Dies ist wohl die einfachste Mglichkeit, um die Wettsteuer zu umgehen. Denn: Die Wettsteuer wird zwar in Deutschland auf alle Wetten fllig. Nicht alle Buchmacher legen Diese jedoch auch auf ihre Spieler um. Es gibt nach wie vor verschiedene Wettanbieter, die die Steuer selbst tragen. Alles was man tun muss, ist einen solchen Anbieter finden und sich bei diesem Anmelden. Ist das geschehen, kann man ganz normal Wetten und seine Einstze ttigen. Im Falle eines Gewinnes wird dieser in voller Hhe ausgezahlt. Die Steuer trgt der Buchmacher.")]),_c('h2',[_vm._v("Top 10 Buchmacher ohne Steuern")]),_vm._m(73),_c('img',{staticClass:"align-left",attrs:{"src":"https://wettsteuer.com/wp-content/uploads/2014/06/Wettsteuer-in-der-Schweiz-1-1.png","alt":"","sizes":"(max-width: 510px) 100vw, 510px"}}),_c('h2',[_vm._v("Gibt es eine Wettsteuer in der Schweiz?")]),_c('p',[_vm._v("Streng genommen gibt es in der Schweiz eine Wettsteuer, sofern man das Wetten als Form des Glcksspiels zhlt  und das tut der Gesetzgeber! Zumindest werden die Gewinne unter bestimmten Voraussetzungen versteuert. In der Schweiz haben die jeweiligen Kantone die gesetzgeberische Gewalt, was das Steuerrecht betrifft. Der Grundsatz Geldspiel im Dienste des Gemeinwohls sollte ursprnglich den Anreiz bieten, dass die Steuerertrge aus Geldspielen an gemeinntzige Projekte flieen. Auch die Glcksspielsucht per se sollte mit diesen Mitteln im Land bekmpft werden. Wer aber geschickt spielt, schleust die Betrge am Gesetzgeber vorbei  und das sogar vllig legal. Der Grund: Wer bei privaten Anbietern spielt, die ihren Sitz bzw. ihre Server nicht in der Schweiz haben, ist auch nicht verpflichtet, dem schweizerischen Steuerrecht nachzukommen.")]),_c('h3',[_vm._v("Freigrenze von 1000 Franken gilt als Grenze in den meisten Kantonen")]),_c('p',[_vm._v("Seit dem Jahr 2013 gilt die Grenze von jhrlich 1000 Franken, ab der dann offiziell steuerliche Abgaben anfallen. Die Begrndung: Wer mehr als 1000 Franken pro Jahr einnimmt, der spielt bzw. wettet professionell. Die Gewinne werden demnach mit einem Einkommen gleichgesetzt  Es greift also das Einkommenssteuerrecht. brigens: Die Situation ist in diesem Fall sowohl in der Schweiz als auch in Deutschland und sterreich identisch: Wer professionell spielt und bestimmte Betrge im Jahr berschreitet, muss damit rechnen, dass der Gewinn mit einem offiziellen Einkommen gleichgesetzt wird.")]),_c('h3',[_vm._v("Viele private Anbieter drngen auf den Markt und ermglichen Schlupflcher im Steuerrecht")]),_c('p',[_vm._v("Lange Jahre war das Glcksspiel in der Schweiz reine Staatsangelegenheit. Wer zum Beispiel bei den staatlichen Lotterien eine Wette platziert, um satte Gewinne abzurumen, der zahlt vllig automatisch  ohne es zu merken  eine Verrechnungssteuer in Hhe von 35%. Diese werden automatisch durch die staatliche Lotterie an den Fiskus abgezwackt. Die Abzge werden den Brgern aber mit Abgabe der Steuererklrung wieder gutgeschrieben. Mit dem Aufkommen der vielen privaten Anbieter durch das Bekanntwerden der Sportwetten im Internet hat sich jedoch einiges gendert. Selbstverstndlich zahlen diese privaten Anbieter keine Verrechnungssteuer an den Staat  mssen sie aber auch nicht: Da sich die Server in nahezu allen Fllen im Ausland und nicht im eigenen Land befinden, gibt es keine steuerrechtliche Verpflichtung, Betrge aus den Wetteinnahmen an den schweizerischen Staat abzutreten.")]),_c('h3',[_vm._v("Wetten bei privaten Anbietern ist erlaubt  Wer hier wettet, zahlt auch in der Schweiz keine Wettsteuer")]),_vm._m(74),_c('img',{staticClass:"align-left",attrs:{"src":"https://wettsteuer.com/wp-content/uploads/2014/06/Wettsteuer-in-%C3%96sterreich-1.png","alt":"","sizes":"(max-width: 510px) 100vw, 510px"}}),_c('h2',[_vm._v("Gibt es eine Wettsteuer in sterreich?")]),_c('p',[_vm._v("Die Gesetzte und Regeln sind in den meisten Lndern gleich, das heit, dass berall eine Einkommenssteuer geleistet werden muss. Obwohl das geltende Recht in sterreich vorsieht, das Glcksspiel, wie auch die stattliche Lotterie, nicht zu dem Einkommen gezhlt wird, so kann es dennoch vorkommen, dass auf den Gewinn eine Steuer fllig wird. Generell allerdings gibt es in sterreich keine Steuer fr Sportwetten.")]),_c('p',[_vm._v("Steuern knnen allerdings geltend gemacht werden, wenn bei einem Wettanbieter Steuern gezahlt werden mssen. Der Wettanbieter kann Steuern abziehen, da dieser Steuern zahlen muss und diese auf den Wetter umlegen kann. Ein Staatsvertrag fr Glckspiel legt fest, dass Wettanbieter fnf Prozent der Einstze fr Wetten direkt an den Staat abzugeben hat. Im Grunde sind sterreichische Kunden davon ausgeschlossen.")]),_c('h3',[_vm._v("Neue Regeln geplant")]),_c('p',[_vm._v("Im Nationalrat wird schon seit einiger Zeit beraten, ob eine Gebhr fr Wetten und Glcksspiel erhoben werden und wie hoch diese ausfallen soll. Wenn eine Einigung vorliegt, dann gilt diese auf fr sterreich und fr die im Ausland gettigten Wetten. Die Mgliche Gebhr soll zunchst bei Einstzen circa zwei Prozent der Einsatzsumme betragen. Bisher war oftmals eine Wetteinsatzgebhr oder eine Gewinnstgebhr notwendig.")]),_c('p',[_vm._v("Die Gewinnstgebhr wurde aus dem Verhltnis zwischen dem Einsatz und der Auszahlungssumme errechnet. Fr sterreich wre die neue Regel fr einen Standort von Wetten dadurch viel interessanter, da fr die Wettbros die Belastung deutlich geringer wre. Fr die Kunden wrde die nderung positive Auswirkungen auf die Gewinnsumme haben und neue Angebote der Wettprodukte knnten ebenfalls in Anspruch genommen werden.")]),_c('h3',[_vm._v("Streit um Wettterminal")]),_c('p',[_vm._v("Eine weitere nderung hat fr Streit gesorgt, der sogar vor Gericht verhandelt wird. Es wurde versucht Glckspiele zu reduzieren, indem man monatlich einen hohen Betrag fr jeden Terminal abziehen wollte, der dem Kriegsopferfonds zuflieen sollte. Die Betreiber wollten sich das nicht gefallen lassen und klagten. In Gegenden wie zum Beispiel Vorarlberg wurden sogenannte kleine Glckspiele wie etwa 