 die 5% auf den Bruttogewinn an, aber tatschlich nur im Gewinnfall. Andere Anbieter verlangen die 5% Steuern auf jeden Einsatz. In der Summe macht dies letztlich kaum einen Unterschied, die Besteuerung der Einstze gilt aber als transparenter. Diese Besteuerung gilt sowohl fr deutsche wie internationale Anbieter, ebenso fr Online-Anbieter.")]),_c('h3',[_vm._v("Wie kam es zur Wettsteuer?")]),_vm._m(77),_c('img',{staticClass:"align-left",attrs:{"src":"https://wettsteuer.com/wp-content/uploads/2014/06/Wie-bezahle-ich-die-Wettsteuer-selber-1.png","alt":""}}),_c('h2',[_vm._v("Wie bezahle ich die Wettsteuer selber?")]),_c('p',[_vm._v("Eine Wettsteuer ist in der Regel von dem Wettanbieter zu entrichten und somit hat der Wetter seine Steuern schon bezahlt, doch es gibt natrlich ausnahmen. Zu den Ausnahmen zhlen Veranstaltungen von Rennen, da wird hier die Rennwettsteuer fllig, die bei einem Finanzamt, das fr den Wetter zustndig ist, gemeldet werden. Bei allen anderen Wetten bernimmt der Wettanbieter die Steuer, da er alleine dafr haftet.")]),_c('p',[_vm._v("Der Gewinn bei der Rennwettsteuer ist binnen einer Woche zu melden, doch man muss beachten, dass dies noch vor Ablauf eines halben Kalendermonats geschieht. Am besten meldet man den Gewinn schnellstmglich, damit man keine Schwierigkeiten bekommt.")]),_c('h3',[_vm._v("Wettsteuer ernst nehmen")]),_c('p',[_vm._v("Im Grunde ist der Steuerschuldner der Wettanbieter, der natrlich keine Umsatzeinbuen haben mchte und somit die Steuer auf die Kunden umlegt. Es gibt allerdings auch Anbieter, die davon absehen, doch hier sollte der Wetter vorsichtig sein, denn oft handelt es sich dabei um Anbieter ohne Lizenz.")]),_c('p',[_vm._v("Aufgrund des Glckspielstaatsvertrages gelten neue Steuerregeln, die grundstzlich eine Steuer fr Glckspiele vorsieht. Diese allerdings sind laut Vertrag Aufgabe der Wettanbieter. So gesehen mssen Wetter sich in der Regel um nichts weiter kmmern. Dennoch legen die meisten Anbieter die Steuer auf die Kunden um