lstein einen Strich durch die Rechnung. Durch die Einfhrung der Wettsteuer konnten die Bundesgesetze Schleswig-Holstein stoppen. Dennoch ist die Wettsteuer keineswegs neu.")]),_c('p',[_vm._v("Fr eine offizielle Lizenz kmpfen viele Anbieter und eine Wettsteuer kommt somit wie gerufen. Es gilt als illegal, wenn ein Internetanbieter keine Lizenzierung vorweisen kann. Dabei wollen die Anbieter natrlich die sogenannten Grauzonen vermeiden, die durch unklare Definierung des Standortes, im Internet, auftritt. In Folge dessen hat die Bundesregierung eine 5-Prozent-Steuer, im Juli 2012, erlassen, die auf jede gewonnene Wette erhoben wird. In Deutschland sollen 20 Lizenzen an verschiedene Anbieter vergeben werden. Anbieter, die als Anwrter gelten berechnen die Steuer auf alle gewonnenen Wetter.")]),_vm._m(79),_c('p',[_vm._v("Nun knnte man sich fragen, warum Anbieter wie Tipico keine Steuer berechnet. Tipico, sowie weitere Anbieter, haben sich dazu entschlossen online noch keine Steuer zu berechnen. In den Wettshops hingegen wird die Wettsteuer bei einer Tippabgabe bereits berechnet. Online dagegen wird darauf ohne weiteres verzichtet. Jedoch knnten noch weitere Anbieter, darunter auch Tipico, die Steuer einfhren und sich eine Lizenz verschaffen.")]),_c('h3',[_vm._v("Wie wird die 5-Prozent-Steuer berechnet?")]),_c('p',[_vm._v("Es gibt zwei Mglichkeiten, wie man die Steuer verrechnen kann. Am hufigsten beziehen sich diese Fnf Prozent auf den Gesamtgewinn der Wette. Recht selten wird die Steuer mit dem Einsatz verrechnet.")]),_vm._m(80),_c('h3',[_vm._v("Welche Vorteile hat die Wettsteuer?")]),_c('p',[_vm._v("Mit der Einfhrung der Wettsteuer wurde das Online-Glcksspiel legalisiert, was zuvor nicht der Fall war. Mit der Legalisierung hat der Kunde nun die Mglichkeit entsprechende Forderungen gegenber Sportwettenanbietern einzuklagen.")]),_c('h3',[_vm._v("Welche Nachteile hat die Wettsteuer?")]),_c('p',[_vm._v("Zumal die Quotenunterschiede einen sehr geringen Prozentsatz an Reingew